@media (min-width: 992px) { 
.img-fluid {
  border-radius: 50%;
  height: 35%;
  width: 35%;
  padding: 20px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

/* h1 animation and styling */
.header{
  font-size: 60px;
  margin-bottom: 0px;
  margin-top: 10px;
  color: white;
}
 
.typewriter{
  position: relative;
  top: 50%;  
  width: 24em;
  margin: 0 auto;
  border-right: 2px solid rgba(255,255,255,.75);
  font-size: 220%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  transform: translateY(-50%);    
}

/* Animation */
.typewriter{
animation: typewriter 4s steps(44) 1s 1 normal both,
           blinkTextCursor 500ms steps(44) infinite normal;
}
@keyframes typewriter{
from{width: 0;}
to{width: 24em;}
}
@keyframes blinkTextCursor{
from{border-right-color: rgba(255,255,255,.75);}
to{border-right-color: transparent;}
}

/* description styling */

p.description{
  text-align: center;
  color: white;
  font-size: 30px;
  margin-top: 0px;
  padding: 20px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 60%;
  font-family: 'Courier New', Courier, monospace;
}
}

@media (max-width: 576.98px) { 

  .img-fluid {
    border-radius: 50%;
    height: 80%;
    width: 80%;
    padding: 20px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
  }
  
    /* h1 animation and styling */
  .header{
    font-size: 40px;
    margin-bottom: 0px;
    margin-top: 10px;
    color: white;
  }
   
  .typewriter{
    position: relative;
    top: 50%;  
    width: 24em;
    margin: 0 auto;
    border-right: 2px solid rgba(255,255,255,.75);
    font-size: 100%;
    text-align: center;
    white-space: nowrap;
    transform: translateY(-50%);    
  }
  
  /* Animation */
  .typewriter{
  animation: typewriter 4s steps(44) 1s 1 normal both,
             blinkTextCursor 500ms steps(44) infinite normal;
  }
  @keyframes typewriter{
  from{width: 0;}
  to{width: 24em;}
  }
  @keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
  }
  
  p.description{
    text-align: center;
    color: white;
    font-size: 20px;
    margin-top: 0px;
    font-family: 'Courier New', Courier, monospace;
  }
      
}

