/* // Medium devices (tablets, 768px and up) */



.navbar{
    background-color: rgb(5, 22, 5);
}
/* navbar styling */
a.nav-link{
    color: white;
    font-size: 20px;
  }
  
  ul {
    padding: 0;
    margin: 0;
    margin-top: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Courier New', Courier, monospace;
  }
    
  ul li {
    list-style: none;
  }
    
  a {
    text-decoration: none;
    color: #ffff;
    font-size: 21px;
    display: block;
    padding: 5px 10px;
    position: relative;
    text-transform: uppercase;
    margin: 0 10px;
    transition: all 0.5s;
  }
  a.nav-link:hover {
    background-color: #f7adc5;
    color: white;
  }
  a:before {
    content: "";
    position: absolute;
    bottom: 12px;
    left: 12px;
    width: 12px;
    height: 12px;
    border: 3px solid #f7adc5;
    border-width: 0 0 3px 3px;
    opacity: 0;
    transition: all 0.3s;
  }
  a:hover:before {
    opacity: 1;
    bottom: -7px;
    left: -7px;
    }
  a:after {
    content: "";
    position: absolute;
    top: 12px;
    right: 12px;
    width: 12px;
    height: 12px;
    border: 3px solid #f7adc5;
    border-width: 3px 3px 0 0;
    opacity: 0;
    transition: all 0.3s;
  }
  a:hover:after {
    opacity: 1;
    top: -7px;
    right: -7px;
  }
